@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Rubik+Dirt&display=swap');
@mixin nameBoardH3AndInput($with){
  font-family: "Rubik Dirt", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 1.17rem; 
  line-height: 1.3; 
  border: none; 
  outline: none; 
  max-width: $with; 
  background-color: transparent;
  color: inherit;
  padding: 0 0.5rem  0 0.5rem;
  box-sizing:content-box;
}


.boardStyle {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}
.header {
    width: 100%;
    height: 6rem;
    background: blueviolet;
    padding: 0 10vw 0 10vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .HeaderLinkHome{
      display:flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      width: 11rem;
      height: 3.5rem;
      background-color: springgreen;
      text-decoration: none;
      text-align: center;
      text-transform: uppercase;
      font-size: 1rem;
    }
    .logo{
      width: 4rem;
      height: 4rem;
      .imageLogo{
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100%;
      }
    }
}
.hideBlockLinks{
  transform: translateX(-100%); 
  opacity: 0;
}
.blokLinks{
    width: 15rem;
    background: gray;
    display: flex;
    flex-direction: column;
    padding: 3.5rem 1rem 1rem 1rem;
    justify-content: flex-start;
    gap: 20px;
    flex-shrink: 0;
    position: relative;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;

    .closeWindowsByBoards{
      position: absolute;
      width: 2rem;
      height: 2rem;
      right: 0;
      top: 50%;
      transform: translate(0%, -50%);
      cursor: pointer;
      border: none;
      background: transparent;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .myBoardsName{
      margin: 0;
      padding: 0;
      font-size: 1.3rem;
    }
    .links{
      display: flex;
      flex-direction: column;
      justify-content: start;
      gap: 0.5rem;
      .linkStyle{
      height: 1.5rem;
      display: flex;
      flex-direction: row;
      justify-content: start;
      text-decoration: none;
      color: black;
      gap: 0.2rem;
      border-radius: 12px;
      padding-left: 0.5rem;
      padding-right: 0.2rem;

      .blockImageBoard{
        display: flex;
        width: 1.5rem;
        height: 100%;
        flex-shrink: 0;
        justify-content: center;
        align-items: center;
        .imageBoard{
          flex-shrink: 0;
          display: block;
          width: 1.2rem;
          height: 1.2rem
        }
      }
      .boardNameText{
        overflow: hidden;
      }
      }
    }
}

.openWindowsByBoards{
  width: 2rem;
  position: relative;
  background: gray;
  cursor: pointer;
  border: none;
    img{
      position: absolute;
      width: 2rem;
      height: 2rem;
      right: 0;
      top: 50%;
      transform: translate(0%, -50%);
      cursor: pointer;
      border: none;
      background: transparent;
    }
}

.positionLinkAndAnother {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  .positionTitleAndList {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow: hidden;
    .name {
      width: 100%;
      height: 6rem;
      background-color: aquamarine;
      overflow: hidden;
      display: flex;
      align-items: center;
      padding-left: 5vw;
        .titleNameBoard{
          overflow: hidden;
          max-width: 50%;
          h3{
            @include nameBoardH3AndInput(100%);
            overflow: hidden;
          }

          input{
            @include nameBoardH3AndInput(100%);
            background: whitesmoke;
          }
        }
    }
    .listBlock {
      position: relative;
      flex-grow: 1;
      overflow: scroll;
      overflow-x: scroll;
      overflow-y: hidden;
      
      .lists {
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
        position: absolute;
        display: flex;
        flex-direction: row;
        padding: 0 6px;
        list-style: none;
        .listSetting {
          display: block;
          padding: 0 6px;
          .listStyle {
            width: 12rem;
            max-height: 100%;
            border-radius: 12px;
            background-color: #f1f2f4;
            color: #44546f;
            box-shadow: 0px 1px 1px #091e4240;
            padding: 1rem;
            h1 {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}

